import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
export default new Router({
  /* 后端支持可开 */
  // mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  base: '/errorpage',
  routes: [
    {path: '/401', component: resolve => require(['components/errorpage/401.vue'], resolve)},
    {path: '/404', component: resolve => require(['components/errorpage/404.vue'], resolve)}
  ]
})



