import fetch from '@/utils/fetch'

export function login (username, password) {
  return fetch({
    url: '/auth/doLogin',
    method: 'post',
    data: {
      username,
      password
    }
  })
}
export function getVerifyCode (width, height) {
  return fetch({
    url: '/auth/getVerifyCode',
    method: 'get',
    params: {
      width,
      height
    }
  })
}
export function getInfo () {
  return fetch({
    url: '/auth/getPermissionByUserId',
    method: 'get'
  })
}

export function logout () {
  return fetch({
    url: '/user/logout',
    method: 'post'
  })
}



