import Vue from 'vue'
import 'babel-polyfill'
import App from './App'
import store from '@/store'
import router from '@/router/error'
import ElementUI from 'element-ui'
Vue.use(ElementUI)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',
  components: { App }
})



