const getters = {
  token: state => state.user.token,
  avatar: state => state.user.avatar,
  trueName: state => state.user.trueName,
  roles: state => state.user.roles,
  permissionList: state => state.permission.permissionList,
  pageSize: state => state.app.pageSize,
  pageNum: state => state.app.pageNum,
  totalCount: state => state.app.totalCount,
  cityList: state => state.app.cityList
}
export default getters



