
/**
 * 通过role.meanUrl判断是否与当前用户权限匹配
 * @param roles
 * @param path
 */
function hasPermission (roles, path) {
  if (roles && roles.length) {
    return roles.filter(role => role.meanUrl === path)
  }
}
// 递归过滤路由表
function filterPermission (list) {
  let newList = []
  if (!list.length) return false
  list.sort((a, b) => { return a.displayOrder - b.displayOrder })
  list.forEach(item => {
    const id = item.id
    const pid = item.parentId
    item['subList'] = []
    list.map(v => {
      // const _id = v.id
      const _pid = v.parentId
      if (id === _pid) {
        item.subList.push(v)
      }
    })
    if (pid === '' || pid === '0') {
      newList.push(item)
    }
  })
  return newList
}
const permission = {
  state: {
    permissionList: [],
    addRouters: []
  },
  actions: {
    GenerateRoutes ({ commit }, data) {
      return new Promise(resolve => {
        const { roles } = data
        const { base } = data
        const role = hasPermission(roles, base)
        commit('SET_PERMISSIONLIST', roles)
        resolve(role)
      })
    }
  },
  mutations: {
    SET_PERMISSIONLIST: (state, permissionList) => {
      state.permissionList = filterPermission(permissionList)
    }
  }
}

export default permission



