import fetch from '@/utils/fetch'
/**
 * 获取省份列表
 */
export function getProvinceList () {
  return fetch({
    url: '/basic/getProvince',
    method: 'get'
  })
}

/**
 * 获取城市列表
 * @export
 */
export function getCityList () {
  return fetch({
    url: '/basic/getRegionAll',
    method: 'get'
  })
}

/**
 * 获得结算房列表
 * @param {any} key
 * @param {any} pageNo
 * @param {any} pageSize
 */
export function getSettlementInfoList ({key, pageNo, pageSize}) {
  return fetch({
    url: '/basic/getSettlementInfoList',
    method: 'get',
    params: {
      key,
      pageNo,
      pageSize
    }
  })
}

/**
 * 上传文件
 * @export
 * @param {any} formData 文件
 */
export function uploadFile (formData, type) {
  return fetch({
    url: '/upload/doUpload?type=' + type,
    method: 'post',
    data: formData
  })
}
/**
 * 查看上传文件
 * @export
 * @param {any} key 文件相对路径
 */
export function getOssByCondition (key) {
  return fetch({
    url: '/upload/getOssByCondition',
    method: 'get',
    params: {key}
  })
}



