// import Cookies from 'js-cookie'
import { getCityList } from '@/api/base'

const app = {
  state: {
    // sidebar: {
    //   opened: !+Cookies.get('sidebarStatus')
    // },
    pageSize: 15,
    pageNum: 1,
    totalCount: 0,
    cityList: []
  },
  mutations: {
    // TOGGLE_SIDEBAR: state => {
    //   if (state.sidebar.opened) {
    //     Cookies.set('sidebarStatus', 1)
    //   } else {
    //     Cookies.set('sidebarStatus', 0)
    //   }
    //   state.sidebar.opened = !state.sidebar.opened
    // },
    set_pageSize (state, pageSize) {
      state.pageSize = pageSize
    },
    set_pageNum (state, pageNum) {
      state.pageNum = pageNum
    },
    set_totalCount (state, totalCount) {
      state.totalCount = totalCount
    },
    set_cityList (state, data) {
      state.cityList = data
    }

  },
  actions: {
    // ToggleSideBar: ({ commit }) => {
    //   commit('TOGGLE_SIDEBAR')
    // },
    async Get_CITYLIST ({ commit }) {
      let response = await getCityList()
      const data = response.returnObject.list
      commit('set_cityList', data)
    },
    SET_PAGESIZE: ({ commit }, pageSize) => commit('set_pageSize', pageSize),
    SET_PAGENUM: ({ commit }, pageNum) => commit('set_pageNum', pageNum),
    SET_TOTALCOUNT: ({ commit }, totalCount) => commit('set_totalCount', totalCount)
  }
}

export default app



