import { login, getInfo } from '@/api/login'
import { getToken, setToken, removeToken, getName, setName, removeName, getAvatar, setAvatar, removeAvatar } from '@/utils/auth'

const user = {
  state: {
    token: getToken(),
    trueName: getName(),
    avatar: getAvatar(),
    roles: []
  },

  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_NAME: (state, trueName) => {
      state.trueName = trueName
    },
    SET_AVATAR: (state, avatar) => {
      state.avatar = avatar
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles
    }
  },

  actions: {
    // 登录
    async Login ({ commit }, userInfo) {
      const username = userInfo.username.trim()
      let response = await login(username, userInfo.password)
      const data = response.returnObject
      setToken(data.token)
      setName(data.user.trueName)
      setAvatar(data.user.avatar)
      commit('SET_TOKEN', data.token)
      commit('SET_NAME', data.user.trueName)
      commit('SET_AVATAR', data.user.avatar)
    },
    // 获取用户信息
    GetInfo ({ commit, state }) {
      return new Promise((resolve, reject) => {
        getInfo().then(res => {
          const data = res.returnObject
          commit('SET_ROLES', data)
          debugger
          resolve(res)
        }).catch(error => {
          reject(error)
        })
      })
    },
    // 前端 登出
    FedLogOut ({ commit }) {
      return new Promise(resolve => {
        commit('SET_TOKEN', '')
        removeToken()
        removeName()
        removeAvatar()
        resolve()
      })
    }
  }
}

export default user



